
import {defineComponent, ref} from 'vue';
import {ScheduleDetailTypeEnum} from "@/components/scheduleDetail/ScheduleDetail.vue";


export default defineComponent({
    name: "ScheduleTypeToggle",
    props: {
        isNewSchedule: {
            type: Boolean
        },
        inputType: {
            type: String
        }
    },

    setup(props: any, context: any) {
        const selectedType = ref<ScheduleDetailTypeEnum>(props.inputType)
        selectedType.value = props.inputType;

        function changeSelectedTypeType(type: ScheduleDetailTypeEnum) {
            selectedType.value = type
            context.emit('update:inputType', type)
        }
        return {
            selectedType, changeSelectedTypeType
        }
    }
})
