<template>
    <div class="caption mt-4" v-if="createType === 'personal' && $CompanyConfig.isAllowPersonal">
        <div class="form-label">
            <span>Employee No</span>
        </div>

        <span v-if="isNewSchedule && employees && employees.length > 0" class="employeeNo-autoComplete">
            <auto-complete :inputList="employees" :optionName="'joinedName'" @selected="selectEmployee"></auto-complete>
        </span>

        <div v-else class="label-detail"
             v-tooltip.top="{content: detail.EmployeeNo, delay: {show: 3000, hide: 100}}">
            {{ detail.EmployeeNo }}
        </div>
    </div>
</template>

<script lang="ts">
import {defineComponent, ref} from 'vue';
import AutoComplete from "@/components/common/AutoComplete.vue";


export default defineComponent({
    name: "ScheduleDetailEmployeeNumber",
    components: {
        AutoComplete
    },
    props: {
        isNewSchedule: {
            type: Boolean
        },
        createType: {
            type: String
        },
        employees: {
            type: Array
        },
        detail: {
            type: Object
        }
    },

    setup(props: any, context: any) {
        function selectEmployee(employee: any) {
            context.emit('selectedEmployee', employee)
        }

        return { selectEmployee }
    }
})
</script>

<style scoped>
.form-label {
    display: inline-block;
    width: 120px;
    font-weight: bold;
    flex-grow: 0;
    flex-shrink: 0;
}

.employeeNo-autoComplete {
    display: inline-block;
    width: 220px;
}
</style>