<template>
    <div class="caption d-flex" v-if="isNewSchedule && $CompanyConfig.isAllowPersonal">
        <div class="d-flex align-center">
            <input type="radio" id="personal" value="personal" class="cursor-pointer" v-model="selectedType" @change="changeSelectedTypeType(selectedType)">
            <label for="personal" class="ml-2 cursor-pointer">Create Personal Schedule</label>
        </div>
        <div class="d-flex align-center ml-6">
            <input type="radio" id="team" value="team" class="cursor-pointer" v-model="selectedType" @change="changeSelectedTypeType(selectedType)">
            <label for="team" class="ml-2 cursor-pointer">Create Team Schedule</label>
        </div>
    </div>
</template>

<script lang="ts">
import {defineComponent, ref} from 'vue';
import {ScheduleDetailTypeEnum} from "@/components/scheduleDetail/ScheduleDetail.vue";


export default defineComponent({
    name: "ScheduleTypeToggle",
    props: {
        isNewSchedule: {
            type: Boolean
        },
        inputType: {
            type: String
        }
    },

    setup(props: any, context: any) {
        const selectedType = ref<ScheduleDetailTypeEnum>(props.inputType)
        selectedType.value = props.inputType;

        function changeSelectedTypeType(type: ScheduleDetailTypeEnum) {
            selectedType.value = type
            context.emit('update:inputType', type)
        }
        return {
            selectedType, changeSelectedTypeType
        }
    }
})
</script>

